.bg {
  width: 100vw;
  height: 100vh;
  background: url(/static/media/bgi.9a1d3b2c.jpg) center center no-repeat;
  background-size: cover;
  position: relative;
}
.bg .title {
  font-size: 64px;
  font-weight: 400;
  background: url(/static/media/bgi.9a1d3b2c.jpg) 40% 60% no-repeat;
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
  position: absolute;
  top: 5%;
  left: 5%;
}
.bg .block {
  width: 200px;
  height: 200px;
  position: absolute;
  top: 30%;
  left: 15%;
}
.bg .block .ant-btn {
  width: 100%;
  height: 50px;
  margin-bottom: 50px;
  opacity: 0.5;
}
